import React, {Component} from 'react';
import Form from '@rjsf/bootstrap-4';
import axios from 'axios';
import {Alert} from "react-bootstrap";

class MainForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            chargers: [],
            isLoaded: false,
            error: null,
        };
    }

    schema = {
        type: "object",
        properties: {
            charger: {
                title: "Select charger",
                type: "string",
                enum: []
            },
            payload: {
                title: "Select request",
                type: "object",
                oneOf: [
                    {
                        title: "Cancel reservation",
                        properties: {
                            customData: {
                                type: "object",
                                properties: {
                                    vendorId: {
                                        type: "string",
                                        maxLength: 255
                                    }
                                }
                            },
                            reservationId: {
                                type: "number",
                                description: "Id of the reservation to cancel"
                            },
                        },
                        required: ["reservationId"]
                    },
                    {
                        title: "Certificate Signed Request",
                        properties: {
                            customData: {
                                type: "object",
                                properties: {
                                    vendorId: {
                                        type: "string",
                                        maxLength: 255
                                    }
                                }
                            },
                            certificateChain: {
                                description: "The signed PEM encoded X.509 certificate. This can also contain the " +
                                    "necessary sub CA certificates. In that case, the order of the bundle should follow " +
                                    "the certificate chain, starting from the leaf certificate.\r\n\r\nThe Configuration " +
                                    "Variable &lt;&lt;configkey-max-certificate-chain-size,MaxCertificateChainSize&gt;&gt; " +
                                    "can be used to limit the maximum size of this field.\r\n",
                                type: "string",
                                maxLength: 10000
                            },
                            certificateType: {
                                type: "string",
                                enum: ["ChargingStationCertificate", "V2GCertificate"]
                            }
                        },
                        required: ["certificateChain"]
                    }
                ]
            }
        }
    }

    componentDidMount() {
        axios.get("http://" + process.env.PUB + ":" + process.env.SERVER.PORT + "/api/get-chargers").then(res => {
                this.setState({
                    isLoaded: true,
                    chargers: res.data
                });
            },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                })
    }

    onSubmit = ({formData}, e) => {
        switch(e.target[1].options.selectedIndex) {
            case 0:
                formData["selected"] = "CancelReservation";
                break;
            case 1:
                formData["selected"] = "CertificateSigned";
                break;
            default:
                break;
        }
        window.alert(JSON.stringify(formData))
    }


    render() {
        const { chargers, isLoaded, error } = this.state;
        if (error) {
            return <Alert variant="danger">{error.message}</Alert>;
        } else if (!isLoaded) {
            return <Alert variant="info">Loading...</Alert>;
        } else {
            this.schema.properties.charger.enum = chargers;
            return (
                <Form schema={this.schema} onSubmit={this.onSubmit} />
            );
        }

    }
}


export default MainForm;