import 'bootstrap/dist/css/bootstrap.min.css'
import React from "react";
import {Col, Container, Navbar, Row} from "react-bootstrap";
import MainForm from "./components/MainForm";
import { LazyLog } from 'react-lazylog';


function App() {

    return (
        <div className="App" style={{backgroundColor: "black", height: "2000px" }}>
            <Container fluid>
                <Navbar bg="dark" expand="lg" className='navbar-dark'>
                    <Navbar.Brand href="#">Ocpp Server web client</Navbar.Brand>
                </Navbar>
            </Container>
            <Container fluid>
                <Row style={{height: "1949px"}}>
                    <Col lg='5' style={{ color: "white" }}>
                        <MainForm/>
                    </Col>
                    <Col>
                        <LazyLog style={{height: "auto"}} enableSearch url={"ws://" + process.env.PUBLIC_URL + "/webclient-socket"} websocketOptions={{
                            onOpen: (e, sock) => {
                                sock.send(JSON.stringify({message: "Socket has been opened!"}))
                            },
                            formatMessage: e => JSON.parse(e).message,
                        }} />
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default App;
